<template>
  <div class="reclama">
    <div class="top-content">
      <h1 class="page-title"></h1>
      <Button class="btn btn-green" @click="showReclamaForm">
        +  
      </Button>
    </div>
    <PageForm />
    <ReclamaTable :data="reclams" />
    <div class="page-bottom">
      <ItemsCounter
          :defaultCount="this.reclams.length"
          :suffix="reclamsLengthModify"
          :titles="titles"
      />
      <Pagination />
    </div>
    <AddReclamaForm
        :isReclamaModal="isReclamaModal"
        @showReclamaForm="showReclamaForm"
        @closeReclamaForm="closeReclamaForm"
    />
  </div>
</template>

<script>
import Button from '../components/button';
import Pagination from "../components/Pagination";
import PageForm from "../containers/PageForm/PageForm";
import ReclamaTable from "../containers/ReclamaTable/ReclamaTable";
import AddReclamaForm from "@/components/Modal/addReclamaForm";
import ItemsCounter from "../components/ItemsCounter";
import { declOfNum } from "../filters/word";


export default {
  name: 'Reclama',
  components: {
    Button, Pagination, PageForm, ReclamaTable, AddReclamaForm, ItemsCounter,
  },
  data() {
    return {
      isReclamaModal: false,
      titles: ['', '', ''],
      reclams: [
        {
          name: 'Everad',
          link: '#'
        },
        {
          name: 'RocketProfit',
          link: '#'
        },
      ]
    }
  },
  computed: {
    reclamsLengthModify() {
      return declOfNum(this.reclams.length, this.titles)
    }
  },
  methods: {
    showReclamaForm() {
      this.isReclamaModal = true
    },

    closeReclamaForm() {
      this.isReclamaModal = false
    },
  }
}
</script>