<template>
  <div class="select" :class="[{'_opened': dropdownVisible, '_selected': modelValue}]">
     <span
         class="select__label"
         :class="{'required-label': isRequired}"
     >{{ label }}</span>
    <div class="select__inner" v-click-outside="() => dropdownVisible = false" :class="{'_error': error}">
      <input type="text"
             :placeholder="span"
             @focus="showDropdown"
             :value="modelValue"
             @input="filterMethod(results, $emit('update:modelValue', $event.target.value))"
      />
      <i class="icon icon-down-open-mini"></i>
      <Dropdown class="select-dropdown"
          :actions="false"
          v-if="dropdownVisible"
      >
        <slot name="addAction" />
        <div
            class="dropdown__label"
            v-for="(item, i) in results" :key="i"
            @click="selectItem(item)"
        >
          {{ item.name }}
        </div>
      </Dropdown>
    </div>
    <span class="error" v-if="error && !modelValue.length">{{ error }}</span>
  </div>
</template>

<script>
import Dropdown from "@/components/Dropdown";
import vClickOutside from "click-outside-vue3";

export default {
  name: 'SearchSelect',
  components: {
    Dropdown
  },
  props: {
    label: String,
    span: String,
    isRequired: Boolean,
    data: Object,
    modelValue: String,
    error: String
  },
  data() {
    return {
      dropdownVisible: false,
      query: '',
      selected: [],
    }
  },
  directives: {
    clickOutside: vClickOutside.directive
  },
  computed: {
    results () {
      return this.filterMethod(this.data, this.modelValue)
    }
  },
  methods: {
    filterMethod (options, query) {
      if (query) {
        return options.filter(option => option['name'].toLowerCase().includes(query.toLowerCase()))
      }

      return options.filter(option => option['name'].toLowerCase())
    },

    selectItem(item) {
      this.$emit('update:modelValue', item.name)
      // this.query = item.name
      this.hideDropdown()
    },

    showDropdown () {
      this.dropdownVisible = true
    },

    hideDropdown () {
      this.dropdownVisible = false
    }
  },
}
</script>

<style lang="scss" scoped>
._error {
  border-color: red;
}
</style>