<template>
    <ModalForm
        v-if="isModalVisible"
        className="modal-teaser"
        title=" "
        @close="closeModal"
    >
      <FormProvider @submitHandler="checkForm">
        <div class="modal-body__inner">
          <div v-if="urlImageSet" class="modal__image--label">
            <div class="crop-wrapper">
              <Button v-if="!prepairImage && !acceptImage && !cropImg" class="btn btn-primary btn-crop" @click.prevent="editorHandler">
                <span class="icon-crop" />
                
              </Button>
              <div class="crop-steps" v-if="cropImageSteps" v-text="' ' + (isCropStepFirst ? '1' : isCropStepSecond ? '2' : '3') + '  3'"></div>
              <div class="crop-type-figure"
                   :class="{'crop-figure-square': isCropStepFirst, 'crop-figure-album': isCropStepSecond, 'crop-figure-straightforward': isCropStepLast}"
                   v-if="cropImageSteps" v-text="(cropImageSteps && isCropStepFirst) ? '' : (cropImageSteps && isCropStepSecond) ? '' : ''"
              ></div>
              <div class="cropped-image-area" v-if="cropImg" >
                <img :src="dataImages[0]" alt="">
              </div>
              <div class="preview-image-area" v-if="urlImageSet && !cropImg" >
                <img :src="urlImageSet" alt="">
              </div>
              <VueCropper
                  v-if="prepairImage"
                  ref="cropper"
                  :src="urlImageSet"
                  alt="Source Image"
                  :aspect-ratio="ratio"
                  drag-mode="move"
                  :movable="true"
                  :crop-box-movable="true"
                  :crop-box-resizable="true"
              />
              <div class="editor-actions" v-if="prepairImage && urlImageSet">
                <Button class="editor-button editor-crop-button" @click="cropImage">
                  <span class="icon"></span>
                  
                </Button>
                <Button class="editor-button editor-cancel-button" @click="clearDataImage">
                  <span class="icon"></span>
                  
                </Button>
              </div>
              <div class="image-actions" v-if="!acceptImage && !prepairImage">
                <Button class="btn btn-primary btn-green save-image" @click="acceptImageHandler">
                  
                </Button>
                <Button class="btn btn-primary btn-red cancel-image" @click="clearDataImage">
                  <div class="close-icon">
                    <span></span>
                    <span></span>
                  </div>
                  
                </Button>
              </div>
            </div>
          </div>
          <label v-else class="modal__image--label">
            <object class="modal__image">
              <h3 class="modal__image--title">  <span>*</span></h3>
              <p class="modal__image--text">
                <span> </span>
                PNG, JPEG, GIF, WEBP
              </p>
              <span class="modal__image--btn">
                <img class="icon" src="@/assets/img/photo.svg" alt="">
                 
              </span>
              <input class="modal__image--input" type="file" required accept="image/*.png, image/*.jpeg, image/*.gif, image/*.webp, image/*.jpg" @change="setImage">
            </object>
            <span class="error" v-if="error">{{ error('pureImages') }}</span>
          </label>
          <div class="modal-body--inputs">
            <InputField
                label=""
                :border="false"
                type="text"
                id="weight"
                placeholder="0,245"
                :readOnly=true
            />
            <InputField
                label=" "
                :border="false"
                id="model"
                placeholder=""
                :readOnly=true
            />
          </div>
          <div class="modal-body--inputs">
            <InputField
                label=" "
                :border="false"
                type="text"
                id="create_date"
                placeholder="01 / 09 / 2021"
                :readOnly=true
            />
            <InputField
                label=" "
                :border="false"
                type="text"
                id="edit_date"
                placeholder="01 / 09 / 2021"
                :readOnly=true
            />
          </div>
          <p class="modal-body__attention">
            ,  <span>*</span>,   <br>
             
          </p>
        </div>
        <div class="modal-body__inner">
          <InputField
              label=""
              placeholder=" "
              id="title"
              type="text"
              v-model="title"
              :counter="200"
              :isRequired="true"
              :error="error('titleError')"
              @clearError="clearError('titleError')"
          />
          <InputField
              label=""
              type="url"
              id="land"
              placeholder=" "
              :spanText="!landLink.length ? '  https://' : HTMLIcon(landLink)"
              v-model="landLink"
              :isRequired="true"
              :error="error('landLinkError')"
              @clearError="clearError('landLinkError')"
              @clearInput="clear('landLink')"
          />
          <InputField
              label=""
              type="url"
              id="transit"
              placeholder=" "
              :spanText="!transitLink.length ? '  https://' : HTMLIcon(transitLink)"
              v-model="transitLink"
              @clearInput="clear('transitLink')"
          />
          <SearchSelect
              :data="categories"
              label=""
              span=" "
              :isRequired="true"
              v-model="categorySelected"
              :error="error('categoryError')"
          >
            <template #addAction>
              <button class="add-action-button" @click="showCategoryForm">
                 
                <span class="icon">+</span>
              </button>
            </template>
          </SearchSelect>
          <SearchSelect
              :data="existingOffers"
              label=""
              span="  "
          >
            <template #addAction>
              <button class="add-action-button" @click="showAddOffer">
                 
                <span class="icon">+</span>
              </button>
            </template>
          </SearchSelect>
          <SearchSelect
              :data="reclama"
              label=""
              span=" "
          >
            <template #addAction>
              <button class="add-action-button" @click="showReclamaForm">
                 
                <span class="icon">+</span>
              </button>
            </template>
          </SearchSelect>
          <div class="modal-body__id">
            <span>ID </span>
            <p class="id">202</p>
          </div>
        </div>
        <div class="modal-buttons">
          <Button class="btn btn-primary" type="submit" @click="checkForm">
            <span class="icon" />
             
          </Button>
        </div>
      </FormProvider>
    </ModalForm>
</template>

<script>
import InputField from "../InputField";
import Button from '@/components/button';
import VueCropper from 'vue-cropperjs';
import 'cropperjs/dist/cropper.css';
import SearchSelect from "@/components/SearchSelect";

import ModalForm from "@/components/Modal/ModalForm";
import FormProvider from "../FormProvider";

export default {
  name: 'ModalTeaser',
  components: {
    InputField, Button, VueCropper, SearchSelect, ModalForm, FormProvider
  },
  props: {
    isModalVisible: {
      type: Boolean,
      default: false
    },
    isAddCategory: Boolean,
    isSubmitForm: Boolean
  },
  emits: ['showAddCategory', 'close', 'showAddOffer', 'showReclamaForm', 'clear', 'submitFlag'],
  data: () => ({
    title: '',
    landLink: '',
    transitLink: '',
    categorySelected: '',
    errors: [],
    acceptImage: false,
    files: [],
    cropImg: '',
    prepairImage: false,
    urlImageSet: false,
    isCropStepFirst: false,
    isCropStepSecond: false,
    isCropStepLast: false,
    cropImageSteps: false,
    dataImages: [],
    ratio: 1,
    reclama: [
      {name: 'LuckyOnline'},
      {name: 'RocketProfit'},
      {name: 'Everad'},
    ],
    categories: [
      {name: ''},
      {name: ''},
      {name: '-'},
      {name: ''},
      {name: ''},
      {name: ''},
      {name: ''},
      {name: ''}
    ],
    formValidated: false
  }),
  watch: {
    ratio: function (newAspectRatio) {
      if (this.isModalVisible && this.$refs.cropper) {
        this.$refs.cropper.setAspectRatio(newAspectRatio)
      }
    },
    isModalVisible: function () {
      if (!this.isModalVisible) {
        setTimeout(() => {
          this.title = ''
          this.landLink = ''
          this.transitLink = ''
          this.categorySelected = ''
          this.acceptImage = false
          this.files = []
          this.errors = []
          this.cropImg = ''
          this.prepairImage = false
          this.urlImageSet = false
          this.isCropStepFirst = false
          this.isCropStepSecond = false
          this.isCropStepLast = false
          this.cropImageSteps = false
          this.dataImages = []
          this.ratio = 1
        }, 500)
      }
    },
  },
  computed: {
    existingOffers() {
      return this.$store.getters.getExistingOffers
    },

    errorsState() {
      return this.$store.getters.getErrors
    }
  },
  methods: {
    closeModal() {
      this.$emit('close', this.isModalVisible)
    },

    acceptImageHandler() {
      this.dataImages.push(this.urlImageSet);
      this.acceptImage = true
    },

    clearDataImage() {
      this.acceptImage = false
      this.files = []
      this.cropImg = ''
      this.prepairImage = false
      this.urlImageSet = false
      this.isCropStepFirst = false
      this.isCropStepSecond = false
      this.isCropStepLast = false
      this.cropImageSteps = false
      this.dataImages = []
      this.ratio = 1
    },

    editorHandler() {
      this.prepairImage = true
      this.isCropStepFirst = true
      this.cropImageSteps = true
    },

    setAspectRatio(ratio) {
      if (this.isCropStepSecond) {
        this.ratio = ratio
      }
    },

    cropImage() {
      // get image data for post processing, e.g. upload or setting image src


      if (this.isCropStepSecond) {
        this.setAspectRatio(2/3)
      }

      if (this.dataImages.length < 1) {
        this.dataImages.push(this.$refs.cropper.getCroppedCanvas().toDataURL());
        this.isCropStepFirst = false
        this.isCropStepSecond = true
        this.setAspectRatio(16/9)
      } else if (this.dataImages.length && this.isCropStepSecond) {
        this.dataImages.push(this.$refs.cropper.getCroppedCanvas().toDataURL());
        this.isCropStepSecond = false
        this.isCropStepLast = true
      } else {
        this.dataImages.push(this.$refs.cropper.getCroppedCanvas().toDataURL());
        this.isCropStepLast = false
        this.cropImageSteps = false
        this.cropImg = true
        this.prepairImage = false
      }
    },

    setImage(e) {
      const file = e.target.files[0];
      if (file.type.indexOf('image/') === -1) {
        alert('Please select an image file');
        return;
      }
      if (typeof FileReader === 'function') {
        const reader = new FileReader();
        reader.onload = (event) => {
          this.urlImageSet = event.target.result;
          // rebuild cropperjs with the updated source
          // this.$refs.cropper.replace(event.target.result);
        };
        reader.readAsDataURL(file);
      } else {
        alert('Sorry, FileReader API not supported');
      }
    },

    showCategoryForm() {
      this.$emit('showAddCategory', this.isAddCategory)
    },

    showAddOffer() {
      this.$emit('showAddOffer', this.isOfferModal)
    },

    showReclamaForm() {
      this.$emit('showReclamaForm', this.isReclamaModal)
    },

    error(key) {
      if (key) {
        return this.errors.map(error => error[key]).join('')
      }
    },

    clear(value) {
      this[value] = ''
    },

    clearError(key) {
      this.errors.find(error => error[key] = '')
    },

    HTMLIcon: function (value) {
      if (this.validateUrl(value)) {
        return '<span class="validate-icon validated"></span>'
      } else {
        return '<span class="validate-icon non-validated"><span></span><span></span></span>'
      }
    },

    validateUrl(value) {
        return /^(https):\/\/[^ "]+$/.test(value)
    },

    checkForm: function () {

      this.errors = []
      console.log(this.title, this.landLink, this.dataImages)

      if (this.title.length
          && this.landLink.length
          && this.validateUrl(this.landLink)
          && this.dataImages.length > 0) {
        this.$emit('submitFlag', this.isSubmitForm)
        console.log(this.title, this.landLink, this.dataImages)
        this.closeModal()
      }

      if (!this.title.length) {
        this.errors.push({
          titleError: this.errorsState.title
        })
      }

      if (!this.landLink.length) {
        this.errors.push({
          landLinkError: this.errorsState.landLink
        })
      }

      if (this.dataImages.length === 0) {
        this.errors.push({
          pureImages: this.errorsState.imageError
        })
      }

      if (!this.categorySelected) {
        this.errors.push({
          categoryError: this.errorsState.categoryError
        })
      }

      if (this.landLink.length && !this.validateUrl(this.landLink)) {
        this.errors.push({
          landLinkError: 'not start with https'
        })
      }
    }
  },
};
</script>

