<template>
  <div class="offers">
    <div class="top-content">
      <h1 class="page-title"></h1>
      <Button class="btn btn-green" @click="showAddOffer">
        +  
      </Button>
    </div>
    <PageForm />
    <OffersTable :data="offers" />
    <div class="page-bottom">
      <ItemsCounter
          :defaultCount="this.offers.length"
          :suffix="offersLengthModify"
          :titles="titles"
      />
      <Pagination />
    </div>
    <AddOfferForm
        :isOfferModal="isOfferModal"
        @showAddOffer="showAddOffer"
        @closeOffer="closeOffer"
    />
  </div>
</template>

<script>
import Button from '../components/button';
import PageForm from "../containers/PageForm/PageForm";
import Pagination from "../components/Pagination";
import OffersTable from "../containers/OffersTable/OffersTable";
import AddOfferForm from "@/components/Modal/AddOfferForm";
import {declOfNum} from "@/filters/word";
import ItemsCounter from "../components/ItemsCounter";

export default {
  name: 'offers-page',
  components: {
    Button, PageForm, Pagination, OffersTable, AddOfferForm, ItemsCounter
  },
  data() {
    return {
      isOfferModal: false,
      titles: ['', '', ''],
    }
  },
  computed: {
    offers() {
      return this.$store.getters.getTableOffers
    },

    offersLengthModify() {
      return declOfNum(this.offers.length, this.titles)
    }
  },
  methods: {
    showAddOffer() {
      this.isOfferModal = true
    },

    closeOffer() {
      this.isOfferModal = false
    },
  }
}
</script>