<template>
  <Modal class-name="modal-news" v-if="isModalVisible">
    <template #header>
      <h2 class="modal-title"> </h2>
      <button
          type="button"
          class="btn-close"
          aria-label="Close modal"
          @click="closeModal"
      >
        <span />
        <span />
      </button>
    </template>
    <template #body>
      <div class="modal-body__inner">
        <div v-if="urlImageSet" class="modal__image--label">
          <div class="crop-wrapper">
            <Button v-if="!prepairImage && !acceptImage && !cropImg" class="btn btn-primary btn-crop" @click.prevent="editorHandler">
              <span class="icon-crop" />
              
            </Button>
            <div class="crop-steps" v-if="cropImageSteps" v-text="' ' + (isCropStepFirst ? '1' : isCropStepSecond ? '2' : '3') + '  3'"></div>
            <div class="crop-type-figure"
                 :class="{'crop-figure-square': isCropStepFirst, 'crop-figure-album': isCropStepSecond, 'crop-figure-straightforward': isCropStepLast}"
                 v-if="cropImageSteps" v-text="(cropImageSteps && isCropStepFirst) ? '' : (cropImageSteps && isCropStepSecond) ? '' : ''"
            ></div>
            <div class="cropped-image-area" v-if="cropImg" >
              <img :src="dataImages[0]" alt="">
            </div>
            <div class="preview-image-area" v-if="urlImageSet && !cropImg" >
              <img :src="urlImageSet" alt="">
            </div>
            <VueCropper
                v-if="prepairImage"
                ref="cropper"
                :src="urlImageSet"
                alt="Source Image"
                :aspect-ratio="ratio"
                drag-mode="move"
                :movable="true"
                :crop-box-movable="true"
                :crop-box-resizable="true"
            />
            <div class="editor-actions" v-if="prepairImage && urlImageSet">
              <Button class="editor-button editor-crop-button" @click="cropImage">
                <span class="icon"></span>
                
              </Button>
              <Button class="editor-button editor-cancel-button" @click="clearDataImage">
                <span class="icon"></span>
                
              </Button>
            </div>
            <div class="image-actions" v-if="!acceptImage && !prepairImage">
              <Button class="btn btn-primary btn-green save-image" @click="acceptImageHandler">
                
              </Button>
              <Button class="btn btn-primary btn-red cancel-image" @click="clearDataImage">
                <div class="close-icon">
                  <span></span>
                  <span></span>
                </div>
                
              </Button>
            </div>
          </div>
        </div>
        <label v-else class="modal__image--label">
          <object class="modal__image">
            <h3 class="modal__image--title">  <span>*</span></h3>
            <p class="modal__image--text">
              <span> </span>
              PNG, JPEG, GIF, WEBP
            </p>
            <span class="modal__image--btn">
                  <img class="icon" src="@/assets/img/photo.svg" alt="">
                   
                </span>
            <input class="modal__image--input" type="file" required accept="image/*.png, image/*.jpeg, image/*.gif, image/*.webp, image/*.jpg" @change="setImage">
          </object>
          <span class="error" v-if="error">{{ error('pureImages') }}</span>
        </label>
        <div class="modal-body--inputs">
          <InputField
              label=" "
              :border="false"
              type="text"
              id="create_date"
              placeholder="01 / 09 / 2021"
              :readOnly=true
          />
          <InputField
              label=" "
              :border="false"
              type="text"
              id="edit_date"
              placeholder="01 / 09 / 2021"
              :readOnly=true
          />
        </div>
        <div class="modal-body--inputs">
          <InputField
              label="ID "
              :border="false"
              type="text"
              id="news_id"
              placeholder="242"
              :readOnly=true
          />
        </div>
        <p class="modal-body__attention">
          ,  <span>*</span>,   <br>
           
        </p>
      </div>
      <div class="modal-body__inner">
        <InputField
            label=""
            placeholder=" "
            id="title"
            type="text"
            v-model="title"
            :counter="200"
            :isRequired="true"
            :error="error('titleError')"
            @clearError="clearError('titleError')"
        />
        <InputField
            label=" "
            type="text"
            id="short_title"
            placeholder=" "
            v-model="short_title"
            :counter="70"
        />
        <SearchSelect
            :data="categories"
            label=""
            span=" "
            :isRequired="true"
            v-model="categorySelected"
            :error="error('categoryError')"
        >
          <template #addAction>
            <button class="add-action-button" @click="showCategoryForm">
               
              <span class="icon">+</span>
            </button>
          </template>
        </SearchSelect>
        <InputField
            label="  "
            type="text"
            id="source_link"
            placeholder=" "
            v-model="source_link"
        />
        <InputField
            label="  "
            type="text"
            id="author"
            placeholder=" "
            v-model="author"
        />
        <div class="multi-inputs">
          <Select
              label=" "
              :data="types"
              span="generalSecondary"
              :actions="false"
              :isRequired="true"
              type="radio"
              v-model="type"
              :error="error('typeError')"
          />
          <InputField
              label=" "
              type="number"
              id="priority"
              placeholder="1"
              v-model="priority"
              :isRequired="true"
              :error="error('priorityError')"
              @clearError="clearError('priorityError')"
          />
        </div>
        <label class="publish-checkbox">
          <span class="publish-checkbox__text"></span>
          <input
              id="publish"
              v-model="isPublish"
              type="checkbox"
              value="publish"
              @change="onChange"
          >
          <span class="publish-checkbox-check"/>
        </label>
      </div>
    </template>
    <template #editor>
      <Tiptap />
    </template>
    <template #footer>
      <Button class="btn btn-primary btn-preview btn-secondary" type="button">
        <span class="icon" />
        
      </Button>
      <Button class="btn btn-primary" type="submit" @click="checkForm">
        <span class="icon" />
        
      </Button>
    </template>
  </Modal>
</template>

<script>
import Modal from "@/containers/Modal";
import InputField from "../InputField";
import Select from "../Select";
import Button from '@/components/button';
import VueCropper from 'vue-cropperjs';
import 'cropperjs/dist/cropper.css';
import Tiptap from "@/components/Tiptap";
import SearchSelect from "@/components/SearchSelect";

export default {
  name: 'ModalNews',
  components: {
    InputField, Select, Modal, Button, VueCropper, Tiptap, SearchSelect
  },
  props: {
    isModalVisible: {
      type: Boolean,
      default: false
    },
    isAddCategory: Boolean
  },
  emits: ['showAddCategory', 'label', 'close'],
  data: () => ({
    title: '',
    short_title: '',
    categorySelected: '',
    source_link: '',
    author: '',
    isPublish: false,
    priority: '',
    type: '',
    acceptImage: false,
    errors: [],
    files: [],
    cropImg: '',
    prepairImage: false,
    urlImageSet: false,
    isCropStepFirst: false,
    isCropStepSecond: false,
    isCropStepLast: false,
    cropImageSteps: false,
    dataImages: [],
    ratio: 1,
    categories: [
      {name: ''},
      {name: ''},
      {name: '-'},
      {name: ''},
      {name: ''},
      {name: ''},
      {name: ''},
      {name: ''}
    ],
    types: [
      {
        option: 'generalSecondary'
      },
      {
        option: 'general'
      },
      {
        option: 'Secondary'
      },
    ]
  }),
  watch: {
    ratio: function (newAspectRatio) {
      if (this.isModalVisible) {
        this.$refs.cropper.setAspectRatio(newAspectRatio)
      }
    },
    isModalVisible: function () {
      if (!this.isModalVisible) {
        setTimeout(() => {
          this.title = ''
          this.short_title = ''
          this.transitLink = ''
          this.categorySelected = ''
          this.source_link = ''
          this.author = ''
          this.isPublish = false
          this.priority = ''
          this.type = ''
          this.errors= []
          this.acceptImage = false
          this.files = []
          this.cropImg = ''
          this.prepairImage = false
          this.urlImageSet = false
          this.isCropStepFirst = false
          this.isCropStepSecond = false
          this.isCropStepLast = false
          this.cropImageSteps = false
          this.dataImages = []
          this.ratio = 1
        }, 500)
      }
    },
  },
  computed: {
    errorsState() {
      return this.$store.getters.getErrors
    }
  },
  methods: {
    closeModal() {
      this.$emit('close', this.isModalVisible)
    },

    acceptImageHandler() {
      this.acceptImage = true
    },

    clearDataImage() {
      this.acceptImage = false
      this.files = []
      this.cropImg = ''
      this.prepairImage = false
      this.urlImageSet = false
      this.isCropStepFirst = false
      this.isCropStepSecond = false
      this.isCropStepLast = false
      this.cropImageSteps = false
      this.dataImages = []
    },

    editorHandler() {
      this.prepairImage = true
      this.isCropStepFirst = true
      this.cropImageSteps = true
    },

    setAspectRatio(ratio) {
      if (this.isCropStepSecond) {
        this.ratio = ratio
      }
    },

    cropImage() {
      // get image data for post processing, e.g. upload or setting image src


      if (this.isCropStepSecond) {
        this.setAspectRatio(2/3)
      }

      if (this.dataImages.length < 1) {
        this.dataImages.push(this.$refs.cropper.getCroppedCanvas().toDataURL());
        this.isCropStepFirst = false
        this.isCropStepSecond = true
        this.setAspectRatio(16/9)
      } else if (this.dataImages.length && this.isCropStepSecond) {
        this.dataImages.push(this.$refs.cropper.getCroppedCanvas().toDataURL());
        this.isCropStepSecond = false
        this.isCropStepLast = true
      } else {
        this.dataImages.push(this.$refs.cropper.getCroppedCanvas().toDataURL());
        this.isCropStepLast = false
        this.cropImageSteps = false
        this.cropImg = true
        this.prepairImage = false
      }
    },

    setImage(e) {
      const file = e.target.files[0];
      if (file.type.indexOf('image/') === -1) {
        alert('Please select an image file');
        return;
      }
      if (typeof FileReader === 'function') {
        const reader = new FileReader();
        reader.onload = (event) => {
          this.urlImageSet = event.target.result;
          // rebuild cropperjs with the updated source
          // this.$refs.cropper.replace(event.target.result);
        };
        reader.readAsDataURL(file);
      } else {
        alert('Sorry, FileReader API not supported');
      }
    },

    onChange: function() {
      this.$emit('label', this.checked);
    },

    showCategoryForm() {
      this.$emit('showAddCategory', this.isAddCategory)
    },

    error(key) {
      if (key) {
        return this.errors.map(error => error[key]).join('')
      }
    },

    clearError(key) {
      this.errors.find(error => error[key] = '')
    },

    checkForm: function () {

      this.errors = []

      if (this.title.length
          && this.priority.length
          && this.type.length
          && this.dataImages.length > 0) {
        this.$emit('submitFlag', this.isSubmitForm)
        this.closeModal()
      }

      if (!this.title.length) {
        this.errors.push({
          titleError: this.errorsState.title
        })
      }

      if (this.dataImages.length === 0) {
        this.errors.push({
          pureImages: this.errorsState.imageError
        })
      }

      if (!this.priority.length) {
        this.errors.push({
          priorityError: this.errorsState.priorityError
        })
      }

      if (!this.categorySelected) {
        this.errors.push({
          categoryError: this.errorsState.categoryError
        })
      }

      if (!this.type) {
        this.errors.push({
          typeError: this.errorsState.typeError
        })
      }
    }
  },
};
</script>

