<template>
  <Modal
      v-if="isModalVisible"
      :class-name="className"
      @submit.prevent="apply"
  >
    <template #header>
      <div class="modal-icon">
        <img :src="img" alt="">
      </div>
      {{ title }}
    </template>
    <template #body v-if="text">
      <p class="modal-text">{{ text }}</p>
    </template>
    <template #footer>
      <Button class="btn btn-primary">
        
      </Button>
      <Button class="btn btn-secondary" type="button" @click="close">
        
      </Button>
    </template>
  </Modal>
</template>

<script>
import Modal from "@/containers/Modal";
import Button from "@/components/button";

export default {
  name: 'AttentionModal',
  components: {
    Modal, Button
  },
  emits: ['cancelClose', 'applyAttention'],
  props: {
    isModalVisible: Boolean,
    text: String,
    title: String,
    className: String,
    img: String
  },
  methods: {
    apply() {
      this.$emit('applyAttention', this.isModalVisible)
    },

    close() {
      this.$emit('cancelClose', this.isModalVisible)
    }
  },
}
</script>