<template>
  <div class="calendar"
       :class="{'opened': opened}"
  >
    <div class="calendar-trigger"
         @click="opened = !opened"
         @close="close"
    >
      <span></span>
      <div class="calendar-trigger__inner">
        {{ intervalLabel }}
        <i class="icon-calendar"></i>
      </div>
    </div>
    <div class="calendar-dropdown"
         v-if="opened"
         v-click-outside="close"
    >
      <div class="calendar-wrapper">
        <ul class="calendar-list">
          <li
              class="calendar-list-item"
              v-for="item in shortcutItems"
              :key="item.id"
              @click="selectShortcut(item.id)"
              :class="{'active': activeItem === item.id}"
          >
            <label>
              {{ item.name }}
<!--              <input-->
<!--                  type="radio"-->
<!--                  name="date"-->
<!--              />-->
            </label>
          </li>
          <li
              class="calendar-list-item"
              :class="{'active': activeItem === null}"
              @click="activeItem = null"
          >
            <label>
              
            </label>
          </li>
        </ul>
        <Calendar
            v-for="(m, i) of months"
            :key="i"
            :month="m"
            :period="selectedPeriod"
            @flip="changeMonth(i, $event)"
            @select="changeEdge"
        />
      </div>
      <div class="calendar-footer">
        <Button class="btn-secondary" type="button" @click="selectShortcut(shortcutItems[0].id)">
          
        </Button>
        <Button class="btn-primary" type="button" @click="accept">
          
        </Button>
      </div>
    </div>
  </div>
</template>

<script>
import moment from 'moment-timezone'
import vClickOutside from "click-outside-vue3";
import Calendar from "@/components/Calendar";
import Button from "@/components/button"

moment.locale('ru')

export default {
  name: 'CalendarContainer',
  components: {Calendar, Button},
  props: {
    value: { type: Array, required: true },
    definition: { type: Object, required: true },
    disabled: { type: Boolean, default: false },
  },
  emits: ['input'],
  data: () => ({
    opened: false,
    focused: false,
    selectedEdge: null,
    months: [null, null],
    activeItem: 0
  }),
  directives: {
    clickOutside: vClickOutside.directive
  },
  computed: {
    shortcutItems () {
      const edge = edge => (what, addendum) => moment()
          .tz(this.timezone)
          .add(addendum)[edge + 'Of'](what)

      const start = edge('start')
      const end = edge('end')

      const range = (what, addendum) => [
        start(what, addendum),
        end(what, addendum),
      ]

      const items = this.definition.shortcuts || {
        '': range('day'),
        '': range('day', { days: -1 }),
        ' 3 ': [start('day', { days: -3 }), end('day')],
        ' 7 ': [start('day', { days: -7 }), end('day')],
        ' ': range('month'),
        ' ': range('month', { months: -1 })
      }

      return Object.keys(items).map((k, i) => ({
        id: i,
        name: k,
        range: items[k],
      }))
    },
    timezone () {
      return this.definition.timezone || 'Europe/Moscow'
    },
    wrappedValue () {
      return [
        moment.tz(this.value[0], this.timezone).startOf('day'),
        moment.tz(this.value[1], this.timezone).endOf('day'),
      ]
    },
    selectedPeriod () {
      return this.selectedEdge
          ? [this.selectedEdge, this.selectedEdge]
          : this.wrappedValue
    },
    intervalLabel () {
      const [from, to] = this.wrappedValue

      const isSame = what => from.isSame(to, what)

      if (isSame('day')) {
        return from.format('D MMMM YYYY')
      }

      if (isSame('month')) {
        return `${from.format('D')}${to.format('D MMM YYYY')}`
      }

      if (isSame('year')) {
        return `${from.format('D MMM')}  ${to.format('D MMM YYYY')}`
      }

      return `${from.format('DD/MM/YYYY')}  ${to.format('DD/MM/YYYY')}`
    },
  },
  watch: {
    opened (newValue) {
      if (!newValue) {
        this.selectedEdge = null
      }
    },
    value () {
      this.initializeMonths()
    },
    definition () {
      this.initializeMonths()
    },
  },
  created () {
    this.initializeMonths()
  },
  methods: {
    selectShortcut (index) {
      this.emitChange(this.shortcutItems[index].range)
      this.setActiveShortcut(index)
    },
    setActiveShortcut(index) {
      this.activeItem = index
    },
    close () {
      this.opened = false
    },
    initializeMonths () {
      const isSameMonth = this.wrappedValue[0].isSame(this.wrappedValue[1], 'month')
      this.months[0] = this.wrappedValue[0]
          .clone()
          .startOf('month')
          .add({ months: isSameMonth ? -1 : 0 })
      this.months[1] = this.wrappedValue[1]
          .clone()
          .startOf('month')
    },
    changeMonth (index, value) {
      if (value.isSame(this.months[1 - index], 'month')) {
        this.months[1 - index] = this.months[1 - index]
            .clone()
            .add({months: index === 0 ? 1 : -1})
      }
      this.months[index] = value
    },
    changeEdge (value) {
      this.activeItem = null
      if (this.selectedEdge == null) {
        this.selectedEdge = value
        this.$emit('input', [value, value])
      } else {
        this.emitChange(this.selectedEdge.isBefore(value)
            ? [this.selectedEdge, value]
            : [value, this.selectedEdge]
        )
      }
    },
    emitChange (newWrappedValue) {

      if (newWrappedValue.length === 2) {
        this.selectedEdge = null
      }

      this.$emit('input', [
        newWrappedValue[0].clone().tz(this.timezone).startOf('day').format(),
        newWrappedValue[1].clone().tz(this.timezone).endOf('day').format(),
      ])

      // this.opened = false
    },
    accept() {
      this.opened = false
    }
  },
}
</script>

