<template>
  <div class="news">
    <div class="top-content">
      <h1 class="page-title"></h1>
      <Button class="btn btn-green" @click="showModal">
        +  
      </Button>
    </div>
    <Tabs v-model="active">
      <Tab></Tab>
      <Tab></Tab>
      <Tab></Tab>
    </Tabs>
    <PageForm />
    <TabPanels v-model="active">
      <TabPanel>
        <NewsTable :data="allNews"/>
        <div class="page-bottom">
          <ItemsCounter
              :defaultCount="allNews.length"
              :suffix="allNewsLength"
              :titles="titles"
          />
          <Pagination />
        </div>
      </TabPanel>
      <TabPanel>
        <NewsTable :data="activeNews"/>
        <div class="page-bottom">
          <ItemsCounter
              :defaultCount="activeNews.length"
              :suffix="activeNewsLength"
              :titles="titles"
          />
          <Pagination />
        </div>
      </TabPanel>
      <TabPanel>
        <NewsTable :data="NotActiveNews"/>
        <div class="page-bottom">
          <ItemsCounter
              :defaultCount="NotActiveNews.length"
              :suffix="nonActiveNewsLength"
              :titles="titles"
          />
          <Pagination />
        </div>
      </TabPanel>
    </TabPanels>
    <ModalNews
        :isModalVisible="isModalVisible"
        @close="closeModal"
        @showAddCategory="showAddCategory"
    />
    <AttentionForm
        :isAttentionMessage="isAttentionMessage"
        :isModalVisible="isModalVisible"
        @cancelClose="closeAttentionMessage"
        @applyAttention="applyCloseModal"
    />
    <AddCategoryForm
        :isAddCategory="isAddCategory"
        @closeCategory="closeCategoryForm"
    />
  </div>
</template>

<script>
import Button from '../components/button';
import PageForm from "../containers/PageForm/PageForm";
import NewsTable from "../containers/NewsTable/NewsTable";
import Pagination from "../components/Pagination";
import ItemsCounter from "../components/ItemsCounter";

import ModalNews from "@/components/Modal/ModalNews";
import AttentionForm from "@/components/Modal/AttentionForm";
import AddCategoryForm from "@/components/Modal/AddCategoryForm";

import Tabs from "@/containers/Tabs";
import Tab from "@/components/Tab";
import TabPanel from "@/components/TabPanel";
import TabPanels from "@/components/TabPanels";

import { declOfNum } from "@/filters/word";

import { ref } from 'vue'

export default {
  name: 'news-page',
  components: {
    Button, PageForm, NewsTable, Pagination, ModalNews, AttentionForm, AddCategoryForm, Tabs, Tab, TabPanel, TabPanels, ItemsCounter
  },
  setup() {
    const active = ref(0);

    return { active };
  },
  data() {
    return {
      titles: ['', '', ''],
      isModalVisible: false,
      isAttentionMessage: false,
      isAddCategory: false,
    }
  },
  computed: {
    allNews() {
      return this.$store.getters.getNews;
    },
    allNewsLength() {
      return declOfNum(this.allNews.length, this.titles)
    },
    activeNews() {
      return this.$store.getters.getActiveNews
    },
    activeNewsLength() {
      return declOfNum(this.activeNews.length, this.titles)
    },
    NotActiveNews() {
      return this.$store.getters.getNonActiveNews
    },
    nonActiveNewsLength() {
      return declOfNum(this.NotActiveNews.length, this.titles)
    },
  },
  methods: {
    showModal() {
      this.isModalVisible = true;
    },

    closeModal() {
      this.isAttentionMessage = true;
    },

    closeAttentionMessage() {
      this.isAttentionMessage = false;
    },

    applyCloseModal() {
      this.isAttentionMessage = false;
      this.isModalVisible = false
    },

    showAddCategory() {
      this.isAddCategory = true
    },

    closeCategoryForm() {
      this.isAddCategory = false
    },
  },
  watch: {
    isModalVisible: function () {
      if (this.isModalVisible) {
        document.body.style.overflow = 'hidden'
        return
      }

      document.body.style.overflow = 'inherit'
    }
  }
}
</script>