<template>
  <Modal
      v-if="isReclamaModal"
      class-name="offer-modal"
  >
    <button
        type="button"
        class="btn-close"
        aria-label="Close modal"
        @click="closeReclamaForm"
    >
      <span />
      <span />
    </button>
    <template #header>
      <div class="modal-icon">
        <img src="@/assets/img/add-icon.svg" alt="">
      </div>
    </template>
    <template #body>
      <form>
        <InputField
            label=""
            type="text"
            id="addReclama"
            placeholder=" "
            :isRequired="true"
            v-model="reclamaName"
        />
        <InputField
            label=""
            type="text"
            id="siteURL"
            placeholder=" "
            v-model="address"
        />
        <InputField
            label=" "
            type="text"
            id="techString"
            placeholder=" "
            v-model="techString"
        />
      </form>
    </template>
    <template #footer>
      <Button class="btn btn-primary add-btn">
        <span class="icon" />
        
      </Button>
      <Button class="btn btn-secondary" @click="closeReclamaForm">
        
      </Button>
    </template>
  </Modal>
</template>

<script>
import Modal from "@/containers/Modal";
import Button from "@/components/button";
import InputField from "@/components/InputField";

export default {
  name: 'AddReclamaForm',
  components: {
    Modal, Button, InputField
  },
  data() {
    return {
      reclamaName: '',
      address: '',
      techString: ''
    }
  },
  emits: ['showReclamaForm', 'closeReclamaForm'],
  props: {
    isReclamaModal: Boolean
  },
  methods: {
    showReclamaForm() {
      this.$emit('showReclamaForm', this.isReclamaModal)
    },
    closeReclamaForm() {
      this.reclamaName = ''
      this.address = ''
      this.techString = ''
      this.$emit('closeReclamaForm', this.isReclamaModal)
    },
  }
}
</script>

