<template>
  <Modal
      v-if="isAttentionMessage"
      class-name="warning-modal"
  >
    <template #header>
      <div class="modal-icon">
        <img src="@/assets/img/warning-icon.svg" alt="">
      </div>
       ,   <br>
       ?
    </template>
    <template #body>
      <p class="modal-text">       </p>
    </template>
    <template #footer>
      <Button class="btn btn-primary" @click="applyAttention">
        
      </Button>
      <Button class="btn btn-secondary" @click="cancelClose">
        
      </Button>
    </template>
  </Modal>
</template>

<script>
import Modal from "@/containers/Modal";
import Button from "@/components/button";

export default {
  name: 'AttentionForm',
  components: {
    Modal, Button
  },
  emits: ['cancelClose', 'applyAttention'],
  props: {
    isAttentionMessage: Boolean,
  },
  methods: {
    applyAttention() {
      this.$emit('applyAttention', this.isAttentionMessage)
    },

    cancelClose() {
      this.$emit('cancelClose', this.isAttentionMessage)
    }
  }
}
</script>