<template>
  <Table>
    <thead>
      <tr>
        <th class="js-sort"><span></span><i class="icon-sort icon"></i></th>
        <th class="js-sort"> <span>ID</span><i class="icon-sort icon"></i></th>
        <th><span></span></th>
        <th class="table-title"><span></span><i class="icon-sort icon"></i></th>
        <th><span></span><i class="icon-sort icon"></i></th>
        <th><span></span><i class="icon-sort icon"></i></th>
        <th><span>.</span><i class="icon-sort icon"></i></th>
        <th class="lang"><span></span><i class="icon-sort icon"></i></th>
        <th><span></span><i class="icon-sort icon"></i></th>
        <th></th>
      </tr>
    </thead>
    <tbody v-if="data.length">
      <tr v-for="teaser in data" :key="teaser.id">
        <td>{{ teaser.date }}</td>
        <td>{{ teaser.id }}</td>
        <td class="table-image">
          <picture>
            <img :src="teaser.img" alt="">
          </picture>
        </td>
        <td class="table-title">{{ teaser.title }}</td>
        <td>
          <div class="category-tag">
            <div class="category" :class="teaser.categoryClass">{{ teaser.category }}</div>
          </div>
        </td>
        <td>{{ teaser.offer }}</td>
        <td> <a class="link" href="">{{ teaser.reclama }}</a></td>
        <td>{{ teaser.lang }}</td>
        <td>{{ teaser.weight }}</td>
        <td>
          <div class="actions">
            <button class="dots" :value="teaser.id" @click="handlerOpenActions">
              <span class="dot"></span>
              <span class="dot"></span>
              <span class="dot"></span>
            </button>
            <div class="actions__inner">
              <a class="actions__item preview" href="#">
                <i class="icon-preview icon"></i>
                <Tooltip text="" />
              </a>
              <a class="actions__item edit" href="#">
                <i class="icon-edit icon"></i>
                <Tooltip text=" " />
              </a>
              <a
                  class="actions__item archive" href="#"
                  @click="showModal(teaser.id)"
                  :data-id="teaser.id"
              >
                <i class="icon-archive icon"></i>
                <Tooltip text="" />
              </a>
            </div>
          </div>
        </td>
      </tr>
    </tbody>
    <tbody v-else>
      <tr>
        <td colspan="10">
          <p class="table-message">    !</p>
        </td>
      </tr>
    </tbody>
    <AttentionModal
        title="   ?"
        :img="img"
        :isModalVisible="isModalVisible"
        v-if="isModalVisible"
        class-name="attention-modal"
        @applyAttention="applyAction(id)"
        @cancelClose="closeModal"
    />
  </Table>
</template>

<style lang="scss">
.modal-body {
  .btn {
    width: 175px;
  }
}

.modal-actions {
  margin-top: 30px;
  display: flex;
  gap: 30px;
}

.modal-icon {
  display: flex;
  flex-direction: column;
  align-items: center;

  img {
    margin-bottom: 30px;
  }
}
</style>

<script>
import Table from "../Table";
import AttentionModal from "../../components/Modal/AttentionModal";
import Tooltip from "../../components/Tooltip";

import { mapMutations } from 'vuex';

export default {
  name: 'TeasersTable',
  components: {
    Table, AttentionModal, Tooltip
  },
  props: {
    data: Object
  },
  data: () => ({
    isHiddenActions: true,
    isModalVisible: false,
    id: null,
    img: require('@/assets/img/archive-icon.svg')
  }),
  methods: {
    handlerOpenActions(e) {
      e.target.parentElement.classList.toggle('_opened')
    },

    ...mapMutations(['archivateTeaser']),

    hideActions() {
      let actions = document.querySelectorAll('.actions');

      actions.forEach(el => {

        if (el.classList.contains('_opened')) {
          el.classList.remove('_opened')
        }
      })
    },

    applyAction() {
      this.archivateTeaser(this.id)
      this.isModalVisible = false;
    },

    showModal(id) {
      this.id = id
      this.isModalVisible = true;
    },

    closeModal() {
      this.isModalVisible = false;
    }
  },
  mounted() {
    let actions = document.querySelectorAll('.actions');

    document.addEventListener('click', function (e) {
      if (!e.target.classList.contains('dots')) {
        actions.forEach(el => {
          el.classList.remove('_opened')
        })
      }
    })
  }
}
</script>