<template>
  <Modal
      v-if="isOfferModal"
      class-name="offer-modal"
  >
    <button
        type="button"
        class="btn-close"
        aria-label="Close modal"
        @click="closeOffer"
    >
      <span />
      <span />
    </button>
    <template #header>
      <div class="modal-icon">
        <img src="@/assets/img/add-icon.svg" alt="">
      </div>
    </template>
    <template #body>
      <form>
        <InputField
            label=" "
            type="text"
            id="offerName"
            placeholder="  "
            :isRequired="true"
            v-model="offerName"
            :icon="visibleIcon"
            icon-class-name="icon-attention"
            :isError="isError"
            :error="error ? pureOfferName : null"
            @keyup="clearInput"
        >
          <template #iconMessage>
            <span class="attention-message">     </span>
          </template>
        </InputField>
        <InputField
            label=" "
            type="text"
            id="sellModal"
            placeholder="  "
            v-model="sellModel"
        />
        <InputField
            label=""
            type="text"
            id="offerReclama"
            placeholder=" "
            v-model="offerReclama"
        />
      </form>
    </template>
    <template #footer>
      <Button class="btn btn-primary add-btn" @click.prevent="submit">
        <span class="icon" />
        
      </Button>
      <Button class="btn btn-secondary" @click="closeOffer">
        
      </Button>
    </template>
  </Modal>
</template>

<script>
import Modal from "@/containers/Modal";
import Button from "@/components/button";
import InputField from "@/components/InputField";

import { mapActions } from "vuex";

export default {
  name: 'AddOfferForm',
  components: {
    Modal, Button, InputField
  },
  emits: ['showAddOffer', 'closeOffer'],
  props: {
    isOfferModal: Boolean
  },
  data() {
    return {
      offerName: '',
      sellModel: '',
      offerReclama: '',
      visibleIcon: false,
      isError: false,
      error: false
    }
  },
  computed: {
    existingOffers() {
      return this.$store.getters.getExistingOffers
    },
    pureOfferName() {
      return this.$store.getters.getOfferNamePureError
    }
  },
  methods: {
    ...mapActions(['addOfferInExisting']),

    submit() {
      this.error = false

      if (this.offerName.length > 0) {
        if (this.existingOffers.find(offer => offer.name.toLowerCase() === this.offerName.toLowerCase())) {
          this.visibleIcon = true
          this.isError = true
        } else {
          this.addOfferInExisting(this.offerName)
          this.closeOffer()
        }
      }

      if (!this.offerName.length) {
        this.error = true
      }
    },

    clearInput() {
      this.visibleIcon = false
      this.isError = false
      this.error = false
    },

    showAddOffer() {
      this.$emit('showAddOffer', this.isOfferModal)
    },

    closeOffer() {
      this.sellModel = ''
      this.offerReclama = ''
      this.offerName = ''
      this.$emit('closeOffer', this.isOfferModal)
    },
  }
}
</script>

