  width: 100%;
  display: flex;
  flex-direction: column;

  @media screen and (max-width: 767px) {
    gap: 15px;
  }

  @media screen and (min-width: 768px) {
    gap: 30px;
  }
}
.btn-close {
  position: absolute;
  right: 20px;
  top: 20px;
}

.attention-message {
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  padding: 10px;
  box-shadow: 0 10px 30px rgba(168, 0, 0, 0.35);
  border-radius: 4px;
  background: #fff;
  font-size: 12px;
  line-height: 16px;
  color: #FF3A3A;
  width: max-content;
}
</style>