<template>
  <div class="input__wrapper">
    <label
        :for="id"
        v-if="labelText"
        :class="{'required-label': isRequired}"
    >{{ label }}</label>
    <div class="input__wrapper--inner"
         :class="[{'_on-focus': onFocus, 'bordered': border, 'flex': spanText || counter, 'active': modelValue, '_error': error}, subClassname]"
    >
      <input
          :type="type"
          :placeholder="placeholder"
          :id="id"
          :readonly="readOnly"
          @focus="onFocus = true"
          :value="modelValue"
          @input="$emit('update:modelValue', $event.target.value)"
          @blur="onFocus = false"
          :maxlength="counter"
          :required="isRequired"
          @keyup="clearOninput(modelValue)"
      >
      <i
          v-if="icon"
          :style="{color: isError ? 'red' : ''}"
          :class="['icon', iconClassName]"
          @mouseenter="showMessage = true"
          @mouseleave="showMessage = false"
      >
        <slot name="iconMessage" v-if="showMessage"/>
      </i>
      <span class="input-prompt" v-if="spanText" v-html="spanText" @click="clearInput"/>
      <span class="input-prompt" v-if="counter">{{ countChars }}</span>
    </div>
    <span class="error" v-if="error">{{ error }}</span>
  </div>
</template>

<script>
export default {
  name: 'InputFiled',
  props: {
    label: String,
    id: String,
    placeholder: String,
    border: {
      type: Boolean,
      default: true
    },
    type: String,
    value: String,
    modelValue: String,
    icon: {
      type: Boolean,
      default: false
    },
    readOnly: {
      type: Boolean,
      default: false
    },
    iconClassName: String,
    labelText: {
      type: Boolean,
      default: true
    },
    spanText: String,
    counter: Number,
    isRequired: {
      type: Boolean,
      default: false
    },
    pattern: String,
    subClassname: String,
    error: String,
    isError: Boolean
  },
  data() {
    return {
      onFocus: false,
      validateURL: this.spanText,
      isValidate: false,
      showMessage: false
    }
  },
  computed: {
    countChars: function () {
      return this.counter - this.modelValue.length
    },
  },
  methods: {
    clearInput() {
      this.$emit('clearInput', this.modelValue)
    },

    clearOninput: function (value) {
      if (value) {
        this.$emit('clearError', this.error)
      }
    },
  },
}
</script>

