      &.non-validated {
        color: red;

        span {
          background-color: red;
          position: absolute;
          transform: rotate(-45deg);
          width: 13px;
          height: 1px;

          &:last-child {
            transform: rotate(45deg);
          }
        }
      }
    }
  }

  &__wrapper {
    cursor: pointer;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    label {
      display: block;
      margin-bottom: 8px;
      text-align: start;

      @media screen and (max-width: 767px) {
        font-size: 10px;
        line-height: 14px;
      }

      @media screen and (min-width: 768px) {
        font-size: 14px;
        line-height: 16px;
      }

      &.required-label:before {
        content: '*';
        position: absolute;
        left: calc(100% + 3px);
        top: 0;
        color: red;
      }
    }

    &--inner {
      background: #fff;
      font-weight: 500;
      padding: 8px 0;
