      }

      &.flex {
        display: flex;
        justify-content: space-between;
        align-items: center;

        input {
          flex: 1;
        }
      }

      &.bordered {
        border: 1px solid rgba(83, 120, 251, 0.25);
        border-radius: 4px;
        padding: 8px 16px;
      }

      .icon {
        position: absolute;
        right: 8px;
        top: 50%;
        transform: translateY(-50%);
        opacity: .4;
        z-index: 10;
      }

      input {
        display: block;
        width: 100%;

        &::placeholder {
          opacity: .4;
        }
        &:focus, &[readonly]::placeholder {
          opacity: 1;
        }
      }

      &._on-focus, &.active {
        border-color: rgba(83, 120, 251, 1);
        span, input, .icon {
          opacity: 1;
        }
      }

      &._error {
        border-color: red;
      }
    }
  }
}
