<template>
  <Table>
    <thead>
    <tr>
      <th class="js-sort"><span></span><i class="icon-sort icon"></i></th>
      <th><span> </span><i class="icon-sort icon"></i></th>
      <th class="js-sort"><span></span><i class="icon-sort icon"></i></th>
      <th class="js-sort"><i class="icon-sort icon"></i></th>
      <th></th>
    </tr>
    </thead>
    <tbody v-if="data.length">
    <tr v-for="(offer, i) in data" :key="i">
      <td>{{ offer.name }}</td>
      <td>{{ offer.sales_model }}</td>
      <td>{{ offer.marja }}</td>
      <td> <a class="link" href="">{{ offer.reclama }}</a></td>
      <td>
        <div class="actions">
          <button class="dots" :value="offer.id" @click="handlerOpenActions">
            <span class="dot"></span>
            <span class="dot"></span>
            <span class="dot"></span>
          </button>
          <div class="actions__inner">
            <a class="actions__item preview" href="#">
              <i class="icon-preview icon"></i>
              <Tooltip text="" />
            </a>
            <a class="actions__item edit" href="#">
              <i class="icon-edit icon"></i>
              <Tooltip text=" " />
            </a>
            <a class="actions__item delete" href="#">
              <i class="icon-trash icon"></i>
              <Tooltip text=" " />
            </a>
          </div>
        </div>
      </td>
    </tr>
    </tbody>
    <tbody v-else>
    <tr>
      <td colspan="5">
        <p class="table-message">    !</p>
      </td>
    </tr>
    </tbody>
  </Table>
</template>

<script>
import Table from "../Table";
import Tooltip from "../../components/Tooltip";

export default {
  name: 'OffersTable',
  components: {
    Table, Tooltip
  },
  props: {
    data: Array
  },
  data: () => ({
    isHiddenActions: true,
  }),
  methods: {
    handlerOpenActions(e) {
      e.target.parentElement.classList.toggle('_opened')
    },

    hideActions(event) {
      let actions = document.querySelectorAll('.actions');

      actions.forEach(el => {

        if (el.classList.contains('_opened')) {
          el.classList.remove('_opened')
          console.log('Clicked outside. Event: ', event.target)
        }
      })
    }
  },
  mounted() {
    let actions = document.querySelectorAll('.actions');

    document.addEventListener('click', function (e) {
      if (!e.target.classList.contains('dots')) {
        actions.forEach(el => {
          el.classList.remove('_opened')
        })
      }
    })
  }
}
</script>