<template>
  <div class="select" :class="[{'_opened': isDropdownOpen, '_selected': checked.length}]">
    <span
        class="select__label"
        :class="{'required-label': isRequired}"
    >
      {{ label }}
    </span>
    <div class="select__inner" @click="toggleDropdown">
      <span v-if="type === 'checkbox'">{{ checked.length ? ' ' + checked.length : span }}</span>
      <span v-else>{{ checked.length ? checked : span }}</span>
      <i class="icon icon-down-open-mini"></i>
    </div>
    <div class="dropdown" v-if="isDropdownOpen" v-click-outside="() => isDropdownOpen = false">
      <div class="dropdown__inner">
        <slot name="addAction" />
        <label class="dropdown__label" @click="selectAll" v-if="type === 'checkbox'">
          <span class="dropdown__text"> </span>
          <input
              v-model="allSelected"
              type="checkbox"
              value="all"
          >
          <span class="dropdown-check" />
        </label>
        <label class="dropdown__label"
               v-for="(option, i) of data"
               :key="i"
        >
          <span class="dropdown__text" :for="option">{{ option }}</span>
          <input
              :id="option"
              v-model="checked"
              :type="type"
              :value="option"
              @change="select"
          >
          <span class="dropdown-check" v-if="type === 'checkbox'" />
        </label>
      </div>
      <div class="dropdown__buttons" v-if="type === 'checkbox'">
        <Button class="btn btn-primary">
          
        </Button>
        <Button class="btn btn-secondary" type="button" @click="clearCheckbox">
          
        </Button>
      </div>
    </div>
  </div>
</template>

<script>
import Button from "@/components/button";
import vClickOutside from "click-outside-vue3";

export default {
  name: 'SelectCheckbox',
  components: {
    Button
  },
  props: {
    data: Array,
    label: String,
    span: String,
    type: String,
    isRequired: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      checked: [],
      allSelected: false,
      isDropdownOpen: false,
    }
  },
  directives: {
    clickOutside: vClickOutside.directive
  },
  methods: {
    toggleDropdown() {
      this.isDropdownOpen = !this.isDropdownOpen
    },

    select() {
      if (this.checked.length < this.data.length) {
        this.allSelected = false
      }
    },

    selectAll: function() {
      this.checked = [];
      if (!this.allSelected) {
        for (let option in this.data) {
          this.checked.push(this.data[option].toString());
        }
      }
    },

    clearCheckbox() {
      this.checked = [];
      this.allSelected = false
    }
  }
}
</script>

<style lang="scss">
