  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  .icon {
    transition: transform .4s ease;
  }

  &._opened {
    .select__inner {
      border-color: rgba(83, 120, 251, 1);
      span, input, .icon {
        opacity: 1;
      }
    }

    .icon {
      transform: rotate(-180deg);
    }
  }

  &._selected {
    .select__inner {
      border-color: rgba(83, 120, 251, 1);
    }

    span, input, .icon {
      opacity: 1;
    }
  }

  &__label {
    display: block;
    margin-bottom: 8px;

    @media screen and (max-width: 767px) {
      font-size: 10px;
      line-height: 14px;
    }

    @media screen and (min-width: 768px) {
      font-size: 14px;
      line-height: 16px;
    }
  }

  &__inner {
    border: 1px solid rgba(83, 120, 251, 0.25);
    border-radius: 4px;
    background: #fff;
    font-weight: 500;
    padding: 8px 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;

    @media screen and (max-width: 767px) {
      font-size: 14px;
      line-height: 19px;
    }

    @media screen and (min-width: 768px) {
      font-size: 18px;
      line-height: 32px;
    }

    .icon {
      right: -8px;
    }

    span {
      opacity: .4;
    }

    > span {
      width: calc(100% - 25px);
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }

    input {
      display: block;
      width: 100%;
      &::placeholder {
        opacity: .4;
      }
      &:focus {
        opacity: 1;
      }
    }
  }
}

.dropdown__label {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-direction: row-reverse;
  padding: 6px 16px;
  transition: background-color .2s ease;

  @media screen and (max-width: 767px) {
    font-size: 14px;
    line-height: 19px;
  }

  @media screen and (min-width: 768px) {
    font-size: 18px;
    line-height: 32px;
  }

  &:hover {
    background: #EAEEFF;
  }

  .dropdown-check {
    margin-right: 14px;
    background: #dfdfff;
    border: 1px solid rgba(124,131,253,.25);
    box-sizing: border-box;
    border-radius: 2px;
    width: 18px;
    height: 18px;
    line-height: 18px;
    text-align: center;
    font-size: 14px;
    color: #fff;
  }

  input {
    width: 0;
    height: 0;
    position: absolute;
    opacity: 0;
    left: 0;
    top: 0;
  }

  input:checked~.dropdown-check {
    background: #7c83fd;
    &::before {
      font-family: 'fontello';
      content: '\e804';
    }
  }

  &.checked {
    background: #DFDFFF;
  }
}
</style>