<template>
  <div class="teasers">
    <div class="top-content">
      <h1 class="page-title"></h1>
      <Button class="btn btn-green" @click="showModal">
        +  
      </Button>
    </div>
    <Tabs v-model="active">
      <Tab></Tab>
      <Tab></Tab>
    </Tabs>
    <PageForm />
    <TabPanels v-model="active">
      <TabPanel>
        <TeasersTable :data="activeTeasers"/>
        <div class="page-bottom">
          <ItemsCounter
              :defaultCount="activeTeasersLength"
              :suffix="teaserLengthModify(activeTeasersLength)"
              :titles="titles"
          />
          <Pagination />
        </div>
      </TabPanel>
      <TabPanel>
        <TeasersTable :data="archivatedTeasers"/>
        <div class="page-bottom">
          <ItemsCounter
              :defaultCount="archivatedTeasers.length"
              :suffix="teaserLengthModify(archivatedTeasers.length)"
              :titles="titles"
          />
          <Pagination />
        </div>
      </TabPanel>
    </TabPanels>
    <ModalTeaser
        :isModalVisible="isModalVisible"
        :isAddCategory="isAddCategory"
        @close="closeModal"
        @showAddCategory="showAddCategory"
        @showAddOffer="showAddOffer"
        @showReclamaForm="showReclamaForm"
        :isModalSubmited="false"
        :isSubmitForm="isSubmitForm"
        @submitFlag="isSubmit"
    />
    <AttentionForm
        :isAttentionMessage="isAttentionMessage"
        @cancelClose="closeAttentionMessage"
        @applyAttention="applyCloseModal"
    />
    <AddCategoryForm
        :isAddCategory="isAddCategory"
        @closeCategory="closeCategoryForm"
    />
    <AddOfferForm
        :isOfferModal="isOfferModal"
        @closeOffer="closeOffer"
    />
    <addReclamaForm
        :isReclamaModal="isReclamaModal"
        @closeReclamaForm="closeReclamaForm"
    />
  </div>
</template>

<script>
import Button from '../components/button';
import PageForm from "../containers/PageForm/PageForm";
import TeasersTable from '../containers/TeasersTable/TeasersTable'
import Pagination from '../components/Pagination'
import ModalTeaser from "@/components/Modal/ModalTeaser";
import AttentionForm from "@/components/Modal/AttentionForm";
import AddCategoryForm from "@/components/Modal/AddCategoryForm";
import AddOfferForm from "@/components/Modal/AddOfferForm";
import addReclamaForm from "@/components/Modal/addReclamaForm";
import {declOfNum} from "@/filters/word";
import ItemsCounter from "../components/ItemsCounter";
import Tabs from "../containers/Tabs";
import Tab from "../components/Tab";
import TabPanel from "@/components/TabPanel";
import TabPanels from "@/components/TabPanels";
import {ref} from "vue";

export default {
  name: 'teasers-page',
  components: {
    Button, PageForm, TeasersTable, Pagination, ModalTeaser, AttentionForm, AddCategoryForm, AddOfferForm, addReclamaForm, ItemsCounter, Tab, Tabs, TabPanel, TabPanels
  },
  setup() {
    const active = ref(0);

    return { active };
  },
  data() {
    return {
      titles: ['', '', ''],
      isModalVisible: false,
      isAttentionMessage: false,
      isDropdownOpen: false,
      isAddCategory: false,
      isOfferModal: false,
      isReclamaModal: false,
      teasers: [],
      isSubmitForm: false
    }
  },
  methods: {
    showModal() {
      this.isModalVisible = true;
    },

    isSubmit() {
      this.isSubmitForm = true
    },

    closeModal() {
      if (!this.isSubmitForm) {
        this.isAttentionMessage = true;
      } else {
        this.isModalVisible = false
        this.isSubmitForm = false
      }
    },

    showAddCategory() {
      this.isAddCategory = true
    },

    closeCategoryForm() {
      this.isAddCategory = false
    },

    showReclamaForm() {
      this.isReclamaModal = true
    },

    closeReclamaForm() {
      this.isReclamaModal = false
    },

    showAddOffer() {
      this.isOfferModal = true
    },

    closeOffer() {
      this.isOfferModal = false
    },

    closeAttentionMessage() {
      this.isAttentionMessage = false;
    },

    applyCloseModal() {
      this.isAttentionMessage = false;
      this.isModalVisible = false
    },

    teaserLengthModify(number) {
      return declOfNum(number, ['', '', ''])
    }
  },
  computed: {
    activeTeasers() {
      return this.$store.getters.activeTeasers;
    },

    archivatedTeasers() {
      return this.$store.getters.archivatedTeasers;
    },

    activeTeasersLength() {
      return this.$store.getters.activeTeasersLength
    },

    teaserLength() {
      return this.teasers.length
    },
  },
  watch: {
    isModalVisible: function () {
      if (this.isModalVisible) {
        document.body.style.overflow = 'hidden'
        return
      }

      document.body.style.overflow = 'inherit'
    },
  },
}
</script>