<template>
  <div
      :class="{'_active': isActive}"
      class="tabs__link"
      @click="activeteTab"
  >
    <slot />
  </div>
</template>

<script>
import { computed, inject, watchEffect, getCurrentInstance } from "vue";

export default {
  name: "Tab",
  setup() {
    const instance = getCurrentInstance();
    const { tabs, selectTab, active } = inject("tabsState", {
      tabs: [],
      selectTab: () => {},
    });
    const index = computed(() =>
        tabs.value.findIndex((target) => target.uid === instance.uid)
    );
    const isActive = computed(() => index.value === active.value);

    const activeteTab = () => {
      selectTab(index.value);
    };

    watchEffect(() => {
      if (index.value === -1) {
        tabs.value.push(instance);
      }
    });

    return {
      activeteTab,
      isActive,
    };
  },
};
</script>

<style lang="scss" scoped>
.tabs {
  &__link {
    display: block;
    opacity: .35;
    border-bottom: 1px solid transparent;
    transition: opacity .2s ease;
    cursor: pointer;

    @media screen and (max-width: 767px) {
      font-size: 14px;
      line-height: 19px;
      padding: 5px 10px;
    }

    @media screen and (min-width: 768px) {
      font-size: 18px;
      line-height: 32px;
      padding: 5px 20px;
    }

    &:hover {
      opacity: 1;
    }

    &._active {
      opacity: 1;
      font-weight: 500;
      border-color: #5378FB;
    }
  }
}
</style>