<template>
  <div class="select" :class="[className, {'_opened': isDropdownOpen, '_selected': modelValue}]" v-click-outside="closeDropdown">
    <span
        class="select__label"
        :class="{'required-label': isRequired}"
    >{{ label }}</span>
    <div class="select__inner" @click="toggleDropdown" :class="{ '_error': error }">
      <slot />
      <span>{{ modelValue ? modelValue : span }}</span>
      <i class="icon icon-down-open-mini"></i>
    </div>
    <Dropdown :class="{'_opened': isDropdownOpen}" :data="data" :actions="actions" v-if="isDropdownOpen">
      <div
          class="dropdown__label"
          v-for="(item, i) in data" :key="i"
          @click="selectItem(item)"
      >
        {{ item.option }}
      </div>
    </Dropdown>
    <span class="error" v-if="error && !modelValue.length">{{ error }}</span>
  </div>
</template>

<script>
import Dropdown from "@/components/Dropdown";
import vClickOutside from "click-outside-vue3";

export default {
  name: 'select-component',
  components: {
    Dropdown
  },
  props: {
    label: String,
    className: String,
    data: Array,
    span: String,
    actions: {
      type: Boolean,
      default: true
    },
    type: String,
    isRequired: {
      type: Boolean,
      default: false
    },
    error: String,
    modelValue: String
  },
  data() {
    return {
      isDropdownOpen: false,
      categorySelected: null,
    }
  },
  directives: {
    clickOutside: vClickOutside.directive
  },
  methods: {
    selectItem(item) {
      this.$emit('update:modelValue', item.option)
      this.closeDropdown()
    },

    toggleDropdown() {
      this.isDropdownOpen = !this.isDropdownOpen
    },

    closeDropdown() {
      this.isDropdownOpen = false
    },
  },
}
</script>

<style lang="scss">
._error {
  border-color: red;
}
</style>