<template>
  <div class="editor">
    <p class="editor-title"></p>
    <div v-if="editor" class="editor__actions">
      <div class="editor__actions--alignment">
        <button @click="editor.chain().focus().setTextAlign('left').run()" :class="{ 'active': editor.isActive({ textAlign: 'left' }) }">
          <i class="icon-text-left"></i>
        </button>
        <button @click="editor.chain().focus().setTextAlign('center').run()" :class="{ 'active': editor.isActive({ textAlign: 'center' }) }">
          <i class="icon-text-center"></i>
        </button>
        <button @click="editor.chain().focus().setTextAlign('right').run()" :class="{ 'active': editor.isActive({ textAlign: 'right' }) }">
          <i class="icon-text-right"></i>
        </button>
        <button @click="editor.chain().focus().setTextAlign('justify').run()" :class="{ 'active': editor.isActive({ textAlign: 'justify' }) }">
          <i class="icon-text-justify"></i>
        </button>
      </div>
      <div class="editor__actions--textstyling">
        <button @click="editor.chain().focus().toggleBold().run()" :class="{ 'active': editor.isActive('bold') }">
          <i class="icon-bold-text"></i>
        </button>
        <button @click="editor.chain().focus().toggleItalic().run()" :class="{ 'active': editor.isActive('italic') }">
          <i class="icon-italic-text"></i>
        </button>
        <button @click="editor.chain().focus().toggleUnderline().run()" :class="{ 'active': editor.isActive('underline') }">
          <i class="icon-underline-text"></i>
        </button>
        <button @click="editor.chain().focus().toggleStrike().run()" :class="{ 'active': editor.isActive('strike') }">
          <i class="icon-strikethrough-text"></i>
        </button>
      </div>
      <div class="editor__actions--list">
        <button @click="editor.chain().focus().toggleBulletList().run()" :class="{ 'active': editor.isActive('bulletList') }">
          <i class="icon-list_bullet"></i>
        </button>
        <button @click="editor.chain().focus().toggleOrderedList().run()" :class="{ 'active': editor.isActive('orderedList') }">
          <i class="icon-list_number"></i>
        </button>
      </div>
      <div class="editor__actions--modify">
        <button @click="editor.chain().focus().toggleBlockquote().run()" :class="{ 'active': editor.isActive('blockquote') }">
          <i class="icon-quote"></i>
        </button>
        <button @click="setLink" :class="{ 'is-active': editor.isActive('link') }">
          <i class="icon-link"></i>
        </button>
        <button @click="addImage">
          <i class="icon-img"></i>
        </button>
      </div>
      <div class="editor__actions--color">
        <label class="color" :style="{backgroundColor: editor.getAttributes('textStyle').color ? editor.getAttributes('textStyle').color : defaultColor}">
          <input
              type="color"
              @input="editor.chain().focus().setColor($event.target.value).run()"
              :value="editor.getAttributes('textStyle').color || defaultColor"
          >
        </label>
      </div>
      <div class="editor__actions--fontSize editor__select">
        <div
            class="editor__actions--fontSize-header editor__select--header"
            @click="fonSizeDropdown = !fonSizeDropdown"
            :class="{'active': fonSizeDropdown}"
        >
          {{ editor.getAttributes('textStyle').fontSize || fontSizeDefault }}
          <span class="icon"></span>
        </div>
        <div class="editor__actions--fontSize-dropdown editor__select--dropdown" v-if="fonSizeDropdown" v-click-outside="() => fonSizeDropdown = false">
          <div class="fontSize-item editor__select--item"
               @click="editor.chain().focus().setFontSize('12px').run()"
               :class="{'active': editor.isActive('textStyle', {fontSize: '12px'})}"
          >12 px</div>
          <div class="fontSize-item editor__select--item"
               @click="editor.chain().focus().setFontSize('14px').run()"
               :class="{'active': editor.isActive('textStyle', {fontSize: '14px'})}"
          >14 px</div>
          <div class="fontSize-item editor__select--item"
               @click="editor.chain().focus().setFontSize('16px').run()"
               :class="{'active': editor.isActive('textStyle', {fontSize: '16px'})}"
          >16 px</div>
          <div class="fontSize-item editor__select--item"
               @click="editor.chain().focus().setFontSize('18px').run()"
               :class="{'active': editor.isActive('textStyle', {fontSize: '18px'})}"
          >18 px</div>
          <div class="fontSize-item editor__select--item"
               @click="editor.chain().focus().setFontSize('24px').run()"
               :class="{'active': editor.isActive('textStyle', {fontSize: '24px'})}"
          >24 px</div>
          <div class="fontSize-item editor__select--item"
               @click="editor.chain().focus().setFontSize('36px').run()"
               :class="{'active': editor.isActive('textStyle', {fontSize: '36px'})}"
          >36 px</div>
          <div class="fontSize-item editor__select--item"
               @click="editor.chain().focus().setFontSize('48px').run()"
               :class="{'active': editor.isActive('textStyle', {fontSize: '48px'})}"
          >48 px</div>
        </div>
      </div>
      <div class="editor__actions--fontFamily editor__select">
        <div
            class="editor__actions--fontFamily-header editor__select--header"
            @click="fontFamilyDropdown = !fontFamilyDropdown"
            :class="{'active': fontFamilyDropdown}"
        >
          {{ editor.getAttributes('textStyle').setFontFamily || fontFamilyDefault }}
          <span class="icon"></span>
        </div>
        <div class="editor__actions--fontFamily-dropdown editor__select--dropdown" v-if="fontFamilyDropdown" v-click-outside="() => fontFamilyDropdown = false">
          <div class="fontFamily-item editor__select--item"
               @click="editor.chain().focus().setFontFamily('Montserrat').run()"
               :class="{'active': editor.isActive('textStyle', {setFontFamily: 'Montserrat'})}"
          >Montserrat</div>
          <div class="fontFamily-item editor__select--item"
               @click="editor.chain().focus().setFontFamily('Helvetica').run()"
               :class="{'active': editor.isActive('textStyle', {setFontFamily: 'Helvetica'})}"
          >Helvetica</div>
          <div class="fontFamily-item editor__select--item"
               @click="editor.chain().focus().setFontFamily('Arial').run()"
               :class="{'active': editor.isActive('textStyle', {setFontFamily: 'Arial'})}"
          >Arial</div>
        </div>
      </div>
    </div>
    <editor-content :editor="editor" class="editor__content" :style="{fontSize: fontSizeDefault}"/>
  </div>
</template>

<script>
import { Editor, EditorContent } from '@tiptap/vue-3'
import StarterKit from '@tiptap/starter-kit'
import TextAlign from '@tiptap/extension-text-align'
import Link from '@tiptap/extension-link'
import Image from '@tiptap/extension-image'
import TextStyle from '@tiptap/extension-text-style'
import { Color } from '@tiptap/extension-color'
import FontSize from '@luciusa/extension-font-size'
import FontFamily from '@tiptap/extension-font-family'
import Underline from '@tiptap/extension-underline'

import vClickOutside from "click-outside-vue3";

export default {
  components: {
    EditorContent,
  },

  data() {
    return {
      editor: null,
      fonSizeDropdown: false,
      fontFamilyDropdown: false,
      fontSizeDefault: null,
      defaultColor: '#00004F',
      fontFamilyDefault: 'Montserrat'
    }
  },

  directives: {
    clickOutside: vClickOutside.directive
  },

  mounted() {

    if (window.innerWidth <= 767) {
      this.fontSizeDefault = '14px'
    } else {
      this.fontSizeDefault = '18px'
    }

    this.editor = new Editor({
      extensions: [
        StarterKit,
        TextAlign.configure({
          types: ['heading', 'paragraph'],
        }),
        Link.configure({
          openOnClick: true,
        }),
        Image,
        Color,
        TextStyle,
        FontSize,
        FontFamily,
        Underline
      ],
      content: ` `,
    })
  },

  methods: {
    setLink() {
      const previousUrl = this.editor.getAttributes('link').href
      const url = window.prompt('URL', previousUrl)
      // cancelled
      if (url === null) {
        return
      }
      // empty
      if (url === '') {
        this.editor
            .chain()
            .focus()
            .extendMarkRange('link')
            .unsetLink()
            .run()

        return
      }
      // update link
      this.editor
          .chain()
          .focus()
          .extendMarkRange('link')
          .setLink({ href: url })
          .run()
    },
    addImage() {
      const url = window.prompt('URL')

      if (url) {
        this.editor.chain().focus().setImage({ src: url }).run()
      }
    },
  }
}
</script>

