<template>
  <div :class="className" class="dropdown">
    <div class="dropdown__inner">
      <div class="dropdown__item">
        <slot />
      </div>
    </div>
    <div class="dropdown__buttons" v-if="actions">
      <Button class="btn btn-primary">
        
      </Button>
      <Button class="btn btn-secondary" type="button" @click="clearCheckbox">
        
      </Button>
    </div>
  </div>
</template>

<script>
import Button from "../components/button";

export default {
  name: 'dropdown-component',
  components: {
    Button
  },
  props: {
    className: String,
    data: Array,
    actions: {
      type: Boolean,
      default: true
    },
    selected: String
  },
  data() {
    return {
      selectedArray: this.selected
    }
  },
  methods: {
    clearCheckbox: function () {
      this.selectedArray = []
      let clearedSelected = this.selectedArray

      this.$emit('clear', clearedSelected)
    }
  },
}
</script>

