<template>
  <Table>
    <thead>
    <tr>
      <th class="js-sort status"><span></span><i class="icon-sort icon"></i></th>
      <th class="js-sort"><span></span><i class="icon-sort icon"></i></th>
      <th class="js-sort"> <span>ID</span><i class="icon-sort icon"></i></th>
      <th></th>
      <th><i class="icon-sort icon"></i></th>
      <th><i class="icon-sort icon"></i></th>
      <th class="lang"><i class="icon-sort icon"></i></th>
      <th></th>
    </tr>
    </thead>
    <tbody v-if="data.length">
      <tr v-for="(news, i) in data" :key="i">
        <td class="status" :class="news.status ? 'active' : 'non-active'">{{ news.status ? '' : '' }}</td>
        <td>{{ news.date }}</td>
        <td>{{ news.id }}</td>
        <td class="table-image">
          <picture>
            <img :src="news.img" alt="">
          </picture>
        </td>
        <td class="table-title">{{ news.title }}</td>
        <td>
          <div class="category-tag">
            <div class="category" :style="categoryConstructor[news.category].styles">{{ categoryConstructor[news.category].name }}</div>
          </div>
        </td>
        <td>{{ news.lang }}</td>
        <td>
          <div class="actions">
            <button class="dots" :value="news.id" @click="handlerOpenActions">
              <span class="dot"></span>
              <span class="dot"></span>
              <span class="dot"></span>
            </button>
            <div class="actions__inner">
              <a class="actions__item preview" href="#">
                <i class="icon-preview icon"></i>
                <Tooltip text="" />
              </a>
              <a class="actions__item edit" href="#">
                <i class="icon-edit icon"></i>
                <Tooltip text="" />
              </a>
              <a class="actions__item delete" href="#" @click="showModal(news.id)">
                <i class="icon-trash icon"></i>
                <Tooltip text=" " />
              </a>
            </div>
          </div>
        </td>
      </tr>
    </tbody>
    <tbody v-else>
      <tr>
        <td colspan="7">
          <p class="table-message">    !</p>
        </td>
      </tr>
    </tbody>
    <AttentionModal
      title=" ,    ?"
      :img="img"
      :isModalVisible="isModalVisible"
      v-if="isModalVisible"
      class-name="attention-modal"
      @applyAttention="applyAction(id)"
      @cancelClose="closeModal"
    />
  </Table>
</template>

<script>
import Table from "../Table";
import AttentionModal from "../../components/Modal/AttentionModal";
import Tooltip from "../../components/Tooltip";

import { mapActions } from "vuex";

export default {
  name: 'NewsTable',
  components: {
    Table, AttentionModal, Tooltip
  },
  props: {
    data: Array
  },
  data: () => ({
    isHiddenActions: true,
    isModalVisible: false,
    isToolipVisible: false,
    id: null,
    img: require('@/assets/img/warning-icon.svg'),
    categoryConstructor: {
      health: {
        name: '',
        styles: {
          color: '#55D740',
          background: 'rgba(85, 215, 64, 0.2)'
        }
      },
      politics: {
        name: '',
        styles: {
          color: '#78A6FF',
          background: 'rgba(120, 166, 255, 0.2)'
        }
      },
      showBusiness: {
        name: '-',
        styles: {
          color: '#B870F0',
          background: 'rgba(184, 112, 240, 0.2)'
        }
      },
      sport: {
        name: '',
        styles: {
          color: '#FFA800',
          background: 'rgba(255, 168, 0, 0.2)'
        }
      }
    },
  }),
  methods: {
    ...mapActions(['deleteNews']),

    showTooltip() {
      this.isToolipVisible = true
    },

    hideTooltip() {
      this.isToolipVisible = false
    },

    handlerOpenActions(e) {
      e.target.parentElement.classList.toggle('_opened')
    },

    hideActions(event) {
      let actions = document.querySelectorAll('.actions');

      actions.forEach(el => {

        if (el.classList.contains('_opened')) {
          el.classList.remove('_opened')
          console.log('Clicked outside. Event: ', event.target)
        }
      })
    },

    applyAction() {
      this.deleteNews(this.id)
      this.isModalVisible = false;
    },

    showModal(id) {
      this.id = id
      this.isModalVisible = true;
    },

    closeModal() {
      this.isModalVisible = false;
    }
  },
  mounted() {
    let actions = document.querySelectorAll('.actions');

    document.addEventListener('click', function (e) {
      if (!e.target.classList.contains('dots')) {
        actions.forEach(el => {
          el.classList.remove('_opened')
        })
      }
    })
  }
}
</script>