<template>
  <div class="table-counter" @click="toggleDropdown">
    <div class="table-counter__header" :class="{'opened': isDropdownOpen}">
      <span class="table-counter__count">
        {{ count }}
        {{ newSuffix }}
      </span>
    </div>
    <ul class="table-counter__dropdown" v-if="isDropdownOpen">
      <li
          class="table-counter__dropdown-item"
          v-for="(count, i) of counterValues"
          :key="i"
          @click="setRowsCount(count)"
      >{{ count }}</li>
    </ul>
  </div>
</template>

<script>
import { declOfNum } from "@/filters/word";

export default {
  name: 'TableCounter',
  props: {
    defaultCount: {
      type: Number
    },
    suffix: String,
    titles: Array
  },
  data() {
    return {
      count: this.defaultCount,
      newSuffix: this.suffix,
      isDropdownOpen: false,
      counterValues: [
          '5', '10', '22', '51', '100'
      ]
    }
  },
  methods: {
    toggleDropdown() {
      this.isDropdownOpen = !this.isDropdownOpen
    },
    setRowsCount(value) {
      this.count = value
      this.newSuffix = declOfNum(value, this.titles)
    }
  },
  watch: {
    defaultCount(newValue) {
      this.count = newValue
    },

    suffix(newValue) {
      this.newSuffix = newValue
    }
  }
}
</script>

<style lang="scss" scoped>
