  font-weight: 500;
  font-size: 16px;
  line-height: 32px;
  min-width: 150px;

  &__header {
    padding: 8px 16px;
    cursor: pointer;
    border: 1px solid transparent;

    &.opened {
      background-color: #fff;
      border-color: #3A58FF;
    }
  }

  &__dropdown {
    position: absolute;
    top: calc(100% + 3px);
    left: 0;
    width: 100%;
    &-item {
      padding: 5px 10px;
      background: #fff;
      transition: color .3s ease, background-color .3s ease;
      cursor: pointer;
      
      &:hover {
        color: #fff;
        background: #3A58FF;
      }
    }
  }
}
</style>